body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{
    background:#EFEFEF; 
    box-shadow: 1px 1px 10px #999; 
    padding-top: 5px;
}

.WeatherCard {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space:nowrap;
}

.WeatherCardListItem{
    box-shadow: 1px 1px 10px #999; 
    border-radius: 5px;
    margin: 2px;
    height: 200px;
    cursor: pointer;
    display:inline-block;
    *display:inline;
    *zoom:1;
    vertical-align:top;
    height: 200px;
    width: 300px;
    margin: 10px 5px;
}

.WeatherCardListing{
    display: flex;
    flex: start 1;
    margin: 5px 5px;
}

.WeatherImg{
    height: 50px;
    width: 50px;
    display: flex;
    margin: 5px 5px;
}

.WeatherTempLayout{
    display: flex;
    justify-content: space-between;
}

.CityMap{
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: -1;
}

.marker {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
  }
.WeatherLineGraph{
    width: 50%;
    display: inline-flex;
}
.button {
    font: inherit;
    border: 1px solid #3b0062;
    color: #3b0062;
    background: #3b0062;
    color: white;
    padding: 0.5rem 1rem;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
  }
  
.InputDropdown{
    width: 100%;
    margin: 10px;
    display: flex;
    justify-content: center;
}


.InputDropdown > .select{
    width:30%;
}

.App-header {
    background-color: #282c34;
    height: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
.MapScreen{
    justify-content: end;
    display: flex;
}

button{
    margin: 30px;
}



.App {
  text-align: center;
}


